import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../template/home/Home'
import MenuOne from '../pages/MenuOne'
import MenuTwo from '../pages/MenuTwo'


Vue.use(VueRouter)


const routes = [{
    name: 'home',
    path: '/',
    component: Home

}, {
    name: 'menuOne',
    path: '/pages',
    component: MenuOne
}, {
    name: 'menuTwo',
    path: '/pages/:id',
    component: MenuTwo
}]


 export default new VueRouter({
     mode: 'history',
     routes
 })

 